import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { FaCheckCircle } from "react-icons/fa"
import "./index.css"

import Sidebar from "../components/sidebar/Sidebar"
// import TechTag from "../components/tags/TechTag"

const AboutPage = (props) => {
    const labels = props.data.site.siteMetadata.labels
    const aboutTags = ["react", "nodejs", "html", "css"]
    const tags = {}
    labels.forEach(label => {
        aboutTags.forEach(tag => {
            if (tag === label.tag) {
                tags[tag] = label.name
            }
        })
    })

    return (
        <Layout>
            <SEO title="About" />
            <div className="post-page-main">
                <div className="sidebar px-4 py-2">
                    <Sidebar />
                </div>
            <div className="post-main">
                    <h2 className="heading">About Linus Lee</h2>
                    <h4>스킬</h4>
                    <div>
                        <span className="text-success d-inline-block" title="blazing">
                            <FaCheckCircle size={26} style={{ color: "success" }} />
                        </span>
                        <p className="d-inline-block ml-3 w-75 align-top">고양이 기르기 <span className="ml-3 text-danger" style={{fontFamily:"Impact" }}>Lv.MAX</span></p>
                    </div>
                    <div>
                        <span className="text-success d-inline-block" title="blazing">
                            <FaCheckCircle size={26} style={{ color: "success" }} />
                        </span>
                        <p className="d-inline-block ml-3 w-75 align-top">Ctrl-C + Ctrl-V <span className="ml-3 text-warning" style={{fontFamily:"Impact" }}>Lv.75</span></p>
                    </div>
                    <div>
                        <span className="text-success d-inline-block" title="blazing">
                            <FaCheckCircle size={26} style={{ color: "success" }} />
                        </span>
                        <p className="d-inline-block ml-3 w-75 align-top">내가 쓴 코드 못 읽기 <span className="ml-3 text-primary" style={{fontFamily:"Impact" }}>Lv.43</span></p>
                    </div>
                    <p></p>
                    <h4>포트폴리오 목록</h4>
                    <div>
                        <span className="text-success d-inline-block" title="blazing">
                            <FaCheckCircle size={26} style={{ color: "success" }} />
                        </span>
                        <p className="d-inline-block ml-3 w-75 align-top">꽉 채울거야... 스크롤바도 생길때까지...</p>
                    </div>
            </div>
        </div>
    </Layout>
)
}

export const pageQuery = graphql`
    query aboutQuery {
        site {
            siteMetadata {
                labels {
                    tag
                    tech 
                    name 
                    size 
                    color
                }
            }
        }
    }
`

export default AboutPage

